.app{
  display: flex;
  width: 100%;
  height: 100%;
  min-height: 100%;
}

.add-user-container, .user-container{
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: 50%;
  padding: 3rem;
  min-height: 50rem;
}

@media only screen and (max-width: 1400px) {
    
  .app{
      flex-direction: column;
  }

  .add-user-container, .user-container{
    width: 88%;
  }  

  .add-user-container{
    min-height: auto;
  }

}

